export default {
	data() {
		return {
			search_key: '',
			list: [],
			isFinish: false,
			page: 1,				
			type:'',
			loading:false,
			city:'',
			tabList:["缴费",'续费'],
			count:0,
			count1:0,
			n:0,
			start:'',
			end:'',
			cityName:''
		}
	},
	onLoad(e) {
		this.type = e.type
		this.cityName = e.cityName
		console.log(this.cityName)
		uni.showLoading({
		    title: '加载中',
			mask:"true"
		});			
		this.getData()
	},
	
	methods: {
		go(item){
			uni.navigateTo({
				url:'../../pagesThree/RenewCityClub/RenewCityClub?id='+item.club_id
			})
		},
		getData () {
		    this.$api.c_report({
			  token: uni.getStorageSync("token"),
			  user_id: uni.getStorageSync("userId"),
		      search: this.search_key,
		      type: this.type,
		      city_name: this.cityName
		    }).then(res => {
				console.log(res)
		      uni.hideLoading();
			  if (res.data.code === '200') {
				  this.list = res.data.data.list
				  this.isFinish = true
			  	}
			 
		    })
		},
		search () {
		  if (this.search_key.length) {
		    uni.showLoading({
		        title: '搜索中',
		      	mask:"true"
		      });
		    }
		    this.getData()
		},
		tel(tel){
		  uni.makePhoneCall({
			phoneNumber:tel
		  })
		},
		my_info (item) {
		  uni.navigateTo({
		  	url:'../../pagesMy/MyInfo/MyInfo?id='+item.user_id
		  })
		}
		
	}
}
